#root {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#site-header {
  height: 60px;
  width: 100%;
  position: absolute;
  background-color: white;
}

#navigation {
  display: flex;
  flex-direction: row;;
  width: 1100px;
  margin: 0 auto;
}

.center {
  max-width: 1100px;
  margin: 0 auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#navigation {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

#pages, #logotype {
  display:flex;
  flex-direction: row;
}

#title {
  font-size: 24px;
  font-weight: 600;
  margin: 0 10px;
}

#pages h1 {
  margin: 0 10px;
  font-size: 22px;
  font-weight: 600;
}

#pages h1:hover {
  cursor: pointer;
  color: #1890ff;
}
