

.blog-entry {
  /* Add styling for the entire blog entry */
  display: flex;
  flex-direction: column;
}

.entry-details {
  display: flex;
  flex-direction: row;
}

.entry-author-info {
  display: flex;
  align-items: center;
}

.entry-author-image {
  width: 50px; /* Adjust the size of the author's image as needed */
  height: 50px;
  border-radius: 50%;
  margin-right: 10px;
}

.entry-author-name {
  padding: 10px;
  font-weight: bold;
}

.entry-date {
  margin-left: auto;
  padding: 10px;
}